"use client";

import { useEffect, useState } from "react";
import VitalsCard from "../components/dashboard/VitalsCard";
import HeartRateChart from "../components/dashboard/HeartRateChart";
import Alerts from "../components/dashboard/Alerts";
import Appointments from "../components/dashboard/Appointments";
import { FaBell, FaCalendarAlt, FaHeartbeat, FaThermometerHalf, FaTint } from "react-icons/fa";

export default function Dashboard() {
  const [bpmData, setBpmData] = useState<{ time: string; bpm: number }[]>([]);
  const [currentBpm, setCurrentBpm] = useState(79);

  useEffect(() => {
    const interval = setInterval(() => {
      const bpm = 60 + Math.floor(Math.random() * 40);
      setCurrentBpm(bpm);
      const now = new Date();
      setBpmData((prev) => [
        ...prev,
        { time: now.toLocaleTimeString(), bpm },
      ].slice(-10));
    }, 2000);

    return () => clearInterval(interval);
  }, []);

  return (
    <div className="min-h-screen bg-gradient-to-br from-purple-700 to-blue-500 text-white flex flex-col">
      <header className="p-6 text-3xl font-bold tracking-wide drop-shadow-lg">
        Dashboard
      </header>

      {/* Top Cards */}
      <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-4 gap-6 px-6">
        <VitalsCard
          label="Heart Rate"
          value={currentBpm}
          unit="bpm"
          icon={
            <FaHeartbeat size={28} className="text-red-400 animate-pulse" />
          }
          className="hover:scale-105 hover:shadow-xl transition-transform duration-300 bg-white/10 backdrop-blur-md"
        />
        <VitalsCard
          label="Blood Pressure"
          value="120/80"
          unit="mmHg"
          icon={<FaThermometerHalf size={28} className="text-yellow-300" />}
          className="hover:scale-105 hover:shadow-xl transition-transform duration-300 bg-white/10 backdrop-blur-md"
        />
        <VitalsCard
          label="Body Temperature"
          value="98.6"
          unit="Â°F"
          icon={<FaThermometerHalf size={28} className="text-orange-300" />}
          className="hover:scale-105 hover:shadow-xl transition-transform duration-300 bg-white/10 backdrop-blur-md"
        />
        <VitalsCard
          label="Oxygen Saturation"
          value="98"
          unit="%"
          icon={<FaTint size={28} className="text-cyan-300" />}
          className="hover:scale-105 hover:shadow-xl transition-transform duration-300 bg-white/10 backdrop-blur-md"
        />
      </div>

      {/* Heart Rate Chart */}
      <div className="px-6 mt-6">
        <HeartRateChart data={bpmData} />
      </div>

      {/* Bottom Components */}
      <div className="mt-6 flex flex-wrap justify-around px-6 gap-6">
        <div className="bg-white/10 hover:bg-white/20 transition duration-300 rounded-xl p-4 flex-1 min-w-[250px] flex items-center gap-4 cursor-pointer shadow-lg hover:scale-105 transform backdrop-blur-md">
          <FaBell size={32} className="text-yellow-300 animate-bounce" />
          <Alerts />
        </div>
        <div className="bg-white/10 hover:bg-white/20 transition duration-300 rounded-xl p-4 flex-1 min-w-[250px] flex items-center gap-4 cursor-pointer shadow-lg hover:scale-105 transform backdrop-blur-md">
          <FaCalendarAlt size={32} className="text-green-300 animate-bounce" />
          <Appointments />
        </div>
      </div>
    </div>
  );
}
